import "../styles/Donation.css"
import cat from "../images/cat1.jpg"

function Donation(){

    return(
        <>
            <section className="donation">
                <div className="image-cat">
                    <img src={cat}></img>
                </div>
                <div className="infos">
                    <div style={{ backgroundColor: "#fff" }}>Estatsticas de abandono de animais</div>
                    <div style={{ backgroundColor: "#fff" }}>infomaes sobre quantidade de animais no abrigo</div>
                    <div style={{ backgroundColor: "#fff" }}>infomaes sobre gastos</div>
                    <div style={{ backgroundColor: "#fff" }}>infomaes pix para doao</div>
                </div>
            </section>

        </>
    );
}

export default Donation;